import dictionary from "./dictionary.json";

export const Tiempo = {
  PERFECTO: 'Pretérito Perfecto',
  INDEFINIDO: 'Pretérito Indefinido',
  IMPERFECTO: 'Pretérito Imperfecto',
  FUTURO_IMP: 'Futuro Imperfecto'
}

export const Group = {
  PERFECTO: {"key": 'PERFECTO', "name": 'Perfecto', "tiempo": Tiempo.PERFECTO},
  INDEFINIDO_AR: {"key": 'INDEFINIDO_AR', "name": 'Indefinido -ar', "tiempo": Tiempo.INDEFINIDO},
  INDEFINIDO_ER_IR: {"key": 'INDEFINIDO_ER_IR', "name": 'Indefinido -er/-ir', "tiempo": Tiempo.INDEFINIDO},
  IMPERFECTO_AR: {"key": 'IMPERFECTO_AR', "name": 'Imperfecto -ar', "tiempo": Tiempo.IMPERFECTO},
  IMPERFECTO_ER_IR: {"key": 'IMPERFECTO_ER_IR', "name": 'Imperfecto -er/-ir', "tiempo": Tiempo.IMPERFECTO},
  FUTURO_IMP: {"key": 'FUTURO_IMP', "name": 'Futuro Imperfecto', "tiempo": Tiempo.FUTURO_IMP},
  FUTURO_IMP_IRR: {"key": 'FUTURO_IMP_IRR', "name": 'Futuro Imperfecto Irregular', "tiempo": Tiempo.FUTURO_IMP},
}

export function getVocab(groupKey) {
  switch (groupKey) {
    case Group.PERFECTO.key:
      return findVocabPerfecto()
    case Group.INDEFINIDO_AR.key:
      return findVocabIndefinido('ar')
    case Group.INDEFINIDO_ER_IR.key:
      return findVocabIndefinido('er').concat(findVocabIndefinido('ir'))
    case Group.IMPERFECTO_AR.key:
      return findVocabImperfecto('ar')
    case Group.IMPERFECTO_ER_IR.key:
      return findVocabImperfecto('er').concat(findVocabImperfecto('ir'))
    case Group.FUTURO_IMP.key:
      return findVocabFuturoImp(null)
    case Group.FUTURO_IMP_IRR.key:
      return findVocabFuturoImp('irr')
    default:
      return null;
  }
}

function findVocabPerfecto() {
  return Object.entries(dictionary).filter(([word, desc]) => {
    return !word.endsWith('se') && desc.comoGustar == null && desc.perfecto?.group == null
  }).map(el => el[0]);
}

function findVocabIndefinido(end) {
  return Object.entries(dictionary).filter(([word, desc]) => {
    return word.endsWith(end) && desc.comoGustar == null && desc.indefinido?.group == null
  }).map(el => el[0]);
}

function findVocabImperfecto(end) {
  return Object.entries(dictionary).filter(([word, desc]) => {
    return word.endsWith(end) && desc.comoGustar == null && desc.imperfecto?.group == null
  }).map(el => el[0]);
}

function findVocabFuturoImp(group) {
  return Object.entries(dictionary).filter(([word, desc]) => {
    return !word.endsWith('se') && desc.comoGustar == null && (desc.futuroImp?.group || null) === group
  }).map(el => el[0]);
}

export function conjugar(tiempo, verbo, persona) {
  if (tiempo === Tiempo.INDEFINIDO) {
    return indefinido(verbo, persona)
  } else if (tiempo === Tiempo.IMPERFECTO) {
    return imperfecto(verbo, persona)
  } else if (tiempo === Tiempo.PERFECTO) {
    return perfecto(verbo, persona)
  } else if (tiempo === Tiempo.FUTURO_IMP) {
    return futuroImp(verbo, persona)
  }
}

export function indefinido(verbo, persona) {
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    const ends = ['é', 'aste', 'ó', 'amos', 'asteis', 'aron']
    return base + ends[persona]
  } else {
    const ends = ['í', 'iste', 'ió', 'imos', 'isteis', 'ieron']
    return base + ends[persona]
  }
}

export function futuroImp(verbo, persona) {
  const root = dictionary[verbo].futuroImp?.root || verbo
  const ends = ['é', 'ás', 'á', 'emos', 'éis', 'án']
  return root + ends[persona]
}

export function imperfecto(verbo, persona) {
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    const ends = ['aba', 'abas', 'aba', 'ábamos', 'abais', 'aban']
    return base + ends[persona]
  } else {
    const ends = ['ía', 'ías', 'ía', 'íamos', 'íais', 'ían']
    return base + ends[persona]
  }

}

export function perfecto(verbo, persona) {
  const haber = ['he', 'has', 'ha', 'hemos', 'habéis', 'han']
  return haber[persona] + ' ' + participio(verbo)

}

function participio(verbo) {
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    return base + 'ado'
  } else {
    return base + 'ido'
  }
}
