import * as React from "react";
import {Card, CardContent} from "@mui/material";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";

export function ExerciseCard({tiempo, progress, persona, hint, exercise}) {

  return <Card sx={{mt: 2}}>
    <CardContent>
      <Grid container spacing={2}>
        <Grid item xs={10}>
          <Typography id="progress" variant="h6" align="left"
                      color="text.secondary">{tiempo}</Typography>
        </Grid>
        <Grid item xs={2}>
          <Typography id="progress" variant="h6" align="right"
                      color="text.secondary">{progress}</Typography>
        </Grid>
        <Grid item xs={12} sx={{mt: 5, mb: 8}}>
          <Typography variant="h5" id="exercise" align="center">
            {persona + ' '}
            <Box sx={{display: "inline"}} color={hint ? "success.main" : "warning.main"}>{exercise}</Box>
          </Typography>
        </Grid>
      </Grid>
    </CardContent>
  </Card>;


}